import React from "react";
import { Redirect, Route } from "react-router-dom";

import { getLocalStorage } from "../../Services/utils/localStorageHelper"

import { authenticateResponse } from "../../types/userInfo";
import Layout from "../components/Layout";

const ProtectedRoute: React.ComponentType<any> = ({ component: Component, ...rest }) => {
	
	const userAuth = getLocalStorage("user", authenticateResponse);
	return (
	
		<Route
			{...rest}
			render={(props) => {
				if (userAuth.isLoggedIn===true) {
				
				if((userAuth.response.userInfo.userTypeId!=26)&&(userAuth.response.userInfo.nationalityId?.toLowerCase()!="kw"))
					{
						return <Layout><Component {...props} /></Layout>;
					}
					else
					{
                      //redirect to you are not permitted to login to this system becauze of nationality or your user type is companies 
					  return (<Redirect
						to={{
							pathname: "/CannotAccessThisSystem",
							state: { from: props.location },
						}}
					/>);
					}
				
				
				}
				else {  

					return (<Redirect
						to={{
							pathname: "/login",
							state: { from: props.location },
						}}
					/>);
				}
			}}
		/>
		
	);
};

export default ProtectedRoute;