import React, { Component } from 'react';

import { Bar, HorizontalBar, ChartData,Radar,Pie } from 'react-chartjs-2';
import * as chartjs from "chart.js";
import { Console } from 'console';
import ColorHash from 'color-hash'
export const HorizontalBarChart = (data: any) => {

  var xArray: any[] = [];
  var yArray: any[] = [];


  console.log("data", data.data);
  ///xArray.push('');
  ///yArray.push(0);
  data?.data.map((value: any) => {
    console.log("value", value["employee"]);
    console.log("value", value["notAssigned"]);
    xArray.push(value["employee"]);
    yArray.push(value["notAssigned"]);
  });

  const colorHash = new ColorHash()
  var backGround: any []=[];
  backGround.push((xArray).map((name:any) => colorHash.hex(name)));
  console.log("background",backGround)
  const dataHorBar: ChartData<chartjs.ChartData> = {
    labels: xArray,
    datasets: [
      {
        barPercentage: 0.7,
        label: '   ',
        data: yArray,
        backgroundColor:backGround[0]
        
      }
    ],

  };
  const options: chartjs.ChartOptions = {

    legend: {
      labels: {
        fontSize: 18,
        fontColor: "Black"
      }
    }
    
  }

  return (

    <div >
      {console.log("xArray", xArray)}
      {console.log("yArray", yArray)}
      {/* <h2>Bar Example (custom size)</h2>
        <Bar
          data={dataBar}
          width={100}
          height={50}
        /> */}

      <Pie width={100}
        height={20}
        data={dataHorBar} options={options} />
    </div>
  );


}
